// The header needs a background of -1 so the picture can fit the screen correctly and the writing can go on top
header {
    background-size: cover; 
    background-position: center; 
    height: 100vh; 
    color: white; 
    z-index: -1;
}

// this is setting the container height and the way things are positioned within the container
.hero-container {
    position: relative;
    display: flex; 
    flex-direction: column; 
    justify-content: space-between; 
    height: 100vh; 
}

// this is making the picture be placed at the top left of the hero-container as this is 
// relative positioned, the z index lets it sit behind the text 
.hero-picture {
    position: absolute;
    top: 0; 
    left: 0;
    height: 100%; 
    width: 100%; 
    object-fit: cover;
    z-index: -1;
}

.hero-title{
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;
    justify-content: center;
    font-size: 1.2rem;
    z-index: 1;
    h1{
        margin-bottom: -10px;
    }

}

.hero-scroll{
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    color: $white;
    margin-bottom: 20px;
    font-size: 1.5rem;
    z-index: 1;
    p{
        margin-bottom: 0;
    }
    .icon-arrow-down{
        font-size: 2rem;
    }
}

/////////////////////////////////////////////////////////////////////////////////////////////////////
/// MEDIA QUERIES
/////////////////////////////////////////////////////////////////////////////////////////////////////

@media(max-width: 767px){
    
    // this is taking of the height of the nav bar in mobile viewports so the header and nav combined are 100vh
    header{
        height: calc(100vh - 76px);
    }

    
    // this is here so we can see the scroll down and arrow in the mobile view view without scrolling
    .hero-scroll{
        margin-bottom: 100px;
    }

}

@media(min-width: 576px) and (max-width: 767px){
    .hero-title{
        font-size: 1.8rem;
    }
    .hero-scroll{
        font-size: 1.7rem;
        .icon-arrow-down{
            font-size: 2rem;
        }
    }
}

@media(min-width: 768px) and (max-width: 991px){
    .hero-title{
        font-size: 1.8rem;
    }
    .hero-scroll{
        font-size: 1.7rem;
        .icon-arrow-down{
            font-size: 2rem;
        }
    }
}

@media(min-width: 1100px){
    .hero-title{
        font-size: 2.2rem;
    }
}